@import "../../assets/index.scss";

.header {
  .app-bar {
    &__container {

    }
    .toolbar {
      &__container {
        display: flex;
        width: 100%;
      }

      &__logo {
        left: 10px;
        font-size: 20px;
        font-weight: 600;
        padding: 10px;
        color: $orange;
      }

      &__top-right {
        display: flex;
        .account {
          padding: 10px;
          margin: auto;
          font-size: 16px;
          font-weight: 500;
          color: $orange;
          margin-right: 10px;
        }
      }
    }
    .button {
      &__connect-wallet {
        max-width: 100px;
      }
    }
  }
}
