@import '../../assets/index.scss';

.footer {
  &__navigation {
    width: fit-content;
    float: right;
    .logout {
      padding-right: 50px;
    }
  }

  .system {
    justify-content: flex-start;
    .wrapper {
      display: flex;
      font-size: 13px;
      justify-content: flex-start;
      margin-left: 50px;
      height: 100%;
      .version,
      .endpoint {
        margin-right: 20px;
        transform: translate(25%, 25%);
        
        span {
          color: $orange;
          font-size: 14px;
          letter-spacing: 1px;
        }
      }
    }
  }
}
