@import '../../../assets/variables';

.wrapper-champion-detail {
  width: 100%;
  padding-top: 20px;
  padding-right: 20px;
  .body {
    padding: 15px;
    .item-field {
      width: 100%;
      .title {
        width: 250px;
        margin-top: auto;
        margin-bottom: auto;
        white-space: nowrap;
        text-align: right;
        padding: 0 15px;
      }
      .info {
        display: flex;
        .MuiInput-root {
          &::before, &::after {
            border: none;
          }
          input, textarea {
            color: $primary;
          }
        }
      }
    }
  }
}
